import _ from 'lodash';
import $ from 'jquery';
import * as store from '../../services/store';

const positions = store.get('split-pane-positions') || {
  'split-pane-center': window.innerWidth / 2 + 'px',
  'split-pane-right': window.innerHeight / 2 + 'px',
  'split-pane-left': window.innerHeight / 2 + 'px'
};

function savePosition(state, action) {
  console.log('split-pane-reducer', state, action, 'savePosition');

  const el = document.querySelector('#' + action.id);

  if (el) {
    let position,
      splitter = $(el).split(),
      orientation = splitter.orientation;

    if (orientation === 'horizontal') {
      position = el.firstChild.clientHeight;
    } else if (orientation === 'vertical') {
      position = el.firstChild.clientWidth;
    }

    if (position) {
      console.log('split-pane-reducer saving', positions);
      positions[action.id] = position + 'px';
      store.set('split-pane-positions', positions);
    }
  }

  return state;
}

export default function (state = positions, action) {
  switch (action.type) {
    case 'SPLIT_PANE_DRAG_END':
      return savePosition(state, action);
    default:
      return state;
  }
}


